<template>
  <div>
    <audio
      loop
      ref="music"
      src="../../assets/music/忍 - 忘れたいはずのなにか.mp3"
    ></audio>
    <!-- 导航栏 -->
    <div class="nav">
      <ul class="nav-1">
        <li><a href="index.html">TOP</a></li>
        <li><a href="#info1">STORY</a></li>
        <li><a href="#transit">CHARACTER</a></li>
        <li><a href="#">GRAPHIC</a></li>
        <li><a href="#">SPECIAL</a></li>
        <li><a href="#">PRODUCT</a></li>
        <li>
          <a href="#" @click="toggle()">
            <div id="musicT" :style="styleMusic"></div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "TheNav",
  data() {
    return {
      music: true,
      styleMusic: {
        backgroundPositionY: 0 + "px",
      },
    };
  },
  methods: {
    toggle() {
      if (this.music) {
        this.$refs.music.play();
        this.music = false;
        this.styleMusic = { backgroundPositionY: -32 + "px" };
      } else {
        this.$refs.music.pause();
        this.music = true;
        this.styleMusic = { backgroundPositionY: 0 + "px" };
      }
    },
  },
};
</script>

<style scoped>
#musicT {
  height: 32px;
  width: 150px;
  background-image: url(../../assets/background/off_bgm.png);
  background-repeat: no-repeat;
  margin-top: 1px;
}
.nav {
  background: -webkit-linear-gradient(
    top,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0.5) 66%,
    rgba(255, 255, 255, 0) 99%
  );
  height: 50px;
}

.nav :visited {
  color: black;
}

.nav a {
  font-size: 1.4rem;
}

.nav a:hover {
  color: white;
  text-shadow: 0px 0px 3px yellowgreen;
}

.nav-1 {
  display: inline-block;
}

.nav-1 li {
  margin-right: 50px;
  float: left;
  font-weight: 500;
}
</style>