<template>
  <div
    id="story"
    style="width: 42.3%; height: 700px; margin: auto; position: relative"
  >
    <!-- 故事标题 -->
    <transition name="showBottom">
      <img
        v-if="scroH > 300"
        src="../../assets/background/story.png"
        alt=""
        style="position: absolute; left: 0px; top: -50px"
      />
    </transition>

    <!-- 故事图片 -->
    <transition name="showRight">
      <img
        class="enlarge"
        v-if="scroH > 300"
        @click="showImg(0)"
        :src="img[0]"
        alt=""
        style="
          height: 200px;
          width: 53.8%;
          position: absolute;
          left: -30%;
          top: 50px;
          border: 2px solid pink;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px hotpink;
        "
      />
    </transition>

    <transition name="showRight">
      <img
        class="enlarge"
        v-if="scroH > 300"
        @click="showImg(1)"
        :src="img[1]"
        alt=""
        style="
          position: absolute;
          height: 200px;
          width: 53.8%;
          left: -30%;
          top: 350px;
          border: 2px solid yellow;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px yellowgreen;
        "
      />
    </transition>

    <transition name="showLeftBlur">
      <img
        class="enlarge"
        v-if="scroH > 300"
        @click="showImg(2)"
        :src="img[2]"
        alt=""
        style="
          position: absolute;
          height: 200px;
          width: 53.8%;
          top: 180px;
          right: -420px;
          border: 2px solid skyblue;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px skyblue;
        "
      />
    </transition>

    <!-- 故事内容文本 -->
    <transition name="showBottomBlur">
      <img
        v-if="scroH > 300"
        src="../../assets/background/txt/story-1.png"
        alt=""
        style="
          width: 18.5%;
          height: 525px;
          position: absolute;
          top: 50px;
          left: 180px;
        "
      />
    </transition>

    <transition name="showLeftBlur">
      <img
        v-if="scroH > 300"
        src="../../assets/background/txt/story-2.png"
        alt=""
        style="
          width: 69.2%;
          height: 525px;
          position: absolute;
          top: 50px;
          right: -100px;
        "
      />
    </transition>
    <div id="mask" v-show="show"></div>
    <img v-show="show" :src="img[num]" alt="" id="img1" class="img" />
    <img
      id="btn"
      @click="closeImg"
      v-show="show"
      src="../../assets/background/img-insert/close.png"
      alt=""
    />
  </div>
</template>

<script>
import "@/assets/CSS/animate.css";
export default {
  name: "TheStory",
  data() {
    return {
      show: false,
      num: 0,
      img: [
        require("../../assets/background/story-img2.png"),
        require("../../assets/background/story-img1.png"),
        require("../../assets/background/img-insert/ETC_e002a.png"),
      ],
    };
  },
  methods: {
    showImg(num) {
      this.show = true;
      this.num = num;
    },
    closeImg() {
      this.show = false;
    },
  },
  props: {
    scroH: {
      typeof: Number,
    },
  },
};
</script>

<style scoped>
#btn {
  position: absolute;
  right: -38%;
  top: -14%;
  width: 50px;
  height: 50px;
  z-index: 10;
}

#btn:hover {
  cursor: pointer;
}

.img {
  position: absolute;
  top: -100px;
  left: -300px;
  z-index: 5;
  width: 1200px;
  height: 675px;
}

#mask {
  position: absolute;
  left: -500px;
  top: -50%;
  height: 150%;
  width: 300%;
  opacity: 0.6;
  z-index: 0;
  background-color: black;
}
.enlarge:hover {
  transform: scale(1.1);
  cursor: pointer;
}
</style>