<template>
  <div class="center">
    <!-- 头部 -->
    <BlogHeader :scroH="scroH"></BlogHeader>

    <!-- 过渡图片 -->
    <div
      id="transit"
      class="bg2"
      :style="{ backgroundPositionY: this.scroH / 20 - 350 + 'px' }"
    >
      <transition name="showLeftBlur">
        <img
          v-if="scroH > 900"
          src="../assets/background/txt/transit-txt.png"
          alt=""
          style="position: absolute; top: 80px; right: 0px"
        />
      </transition>
    </div>

    <!-- 第二张长背景 -->
    <div
      id="atri-bg"
      class="bg1"
      :style="{ backgroundPositionY: this.scroH / 20 - 200 + 'px' }"
    >
      <!-- 角色文本 -->
      <div id="character">
        <transition name="showBottom">
          <img
            v-if="scroH > 1200"
            src="../assets/background/character.png"
            alt=""
          />
        </transition>
      </div>

      <!--  -->
    </div>

    <TheCharacter :scroH="scroH"></TheCharacter>

    <!-- 樱花背景 -->
    <div
      class="bg1"
      style="height: 800px"
      :style="{ backgroundPositionY: this.scroH / 20 - 200 + 'px' }"
    >
      <transition name="fadeIn">
        <div v-if="scroH > 2400">
          <TheVideo></TheVideo>
        </div>
      </transition>
    </div>

    <!-- 蓝天背景 -->
    <div id="blueSky" class="bg2"></div>

    <!-- 樱花背景 -->
    <div
      class="bg1"
      :style="{ backgroundPositionY: this.scroH / 20 - 200 + 'px' }"
    ></div>

    <!-- 页脚部分 -->
    <footer>
      <!-- 花圈 -->
      <div class="footer"></div>
    </footer>
  </div>
</template>

<script>
import "@/assets/CSS/animate.css";
import "@/assets/CSS/blog.css";
import TheVideo from "./content/TheVideo.vue";
import TheCharacter from "./content/TheCharacter.vue";
import BlogHeader from "./content/BlogHeader.vue";
export default {
  name: "MyHead",
  components: {
    TheVideo,
    TheCharacter,
    BlogHeader,
  },
  data() {
    return {
      scroH: 0,
    };
  },
  methods: {
    //监听滚动方法
    handleScroll() {
      // console.log(document.documentElement.scrollTop, '距离顶部的距离')
      this.scroH = document.documentElement.scrollTop;
    },
  },
  computed: {
    sakuraFall() {
      return this.scroH / 20 - 200;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
};
</script>

<style>
#character {
  width: 42.3%;
  height: 100px;
  margin: auto;
  position: absolute;
  left: 450px;
  top: 20px;
}
#atri-bg {
  position: relative;
  background-image: url(../assets/background/atri-bg.png);
  background-size: 100%;
}
#transit {
  height: 300px;
  background-image: url(../assets/background/bg02fp.jpg);
  position: relative;
}

#blueSky {
  background-image: url(../assets/background/space03.png);
  background-attachment: fixed;
  height: 200px;
}
</style>